import React, { Component } from "react";
import { Form, Input, Button, Spin, Row, Col, Alert } from "antd";
import { CommonHeader } from "../../containers";
import Amccvector from "../../assets/images/amcc-vector.svg";
import FloatLabel from "../form/FloatLabels";
import "./commonLogin.scss";
import { connect } from "react-redux";
import { login } from "../../actions/auth";
import { Redirect } from "react-router-dom";

const layout = {
  wrapperCol: {
    span: 24,
  },
};
const tailLayout = {
  wrapperCol: {
    span: 24,
  },
};

class Login extends Component {
  constructor(props) {
    super(props);
    this.handleLogin = this.handleLogin.bind(this);
    this.onChangeUsername = this.onChangeUsername.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.state = {
      email: "",
      password: "",
      page: "is_amcc_user",
      error: "false",
      loading: false,
    };
  }
  onChangeUsername(e) {
    this.setState({
      email: e.target.value,
    });
  }
  onChangePassword(e) {
    this.setState({
      password: e.target.value,
    });
  }

  handleLogin() {
    this.setState({
      loading: true,
    });
    const { dispatch, history } = this.props;

    dispatch(login(this.state.email, this.state.password, this.state.page))
      .then(() => {
        history.push("/amcc/manageorganizations");
        window.location.reload();
      })
      .catch(() => {
        this.setState({
          loading: false,
          error: "true",
        });
      });
  }
  canSubmit() {
    const { email, password } = this.state;
    return email.length > 0 && password.length > 0;
  }

  render() {
    const isEnabled = this.canSubmit();
    const { isLoggedIn, message } = this.props;
    if (isLoggedIn) {
      return <Redirect to="/amcc/manageorganizations" />;
    }

    return (
      <>
        <Row className="px-4 py-1 py-lg-3 header">
          <CommonHeader />
        </Row>
        <Row className="content-center mt-0 px-4">
          <Col
            sm={24}
            lg={12}
            className="vector-area justify-content-center mt-1"
          >
            <Col span={24} className="text-center">
              <h5 className="text-center primary-text">
                Control Center and Insights Dashboard
              </h5>
            </Col>
            <Row className="justify-content-center mt-3">
              <img src={Amccvector} alt="Anonymoose" className="pr-0 pr-lg-4" />
            </Row>
            <Row className="pt-3">
              <p className="w-100 text-center text-wgt-md mb-1 d-none d-lg-block">
                Raising Voice Anonymously Made Easy
              </p>
            </Row>
          </Col>
          <Col
            lg={{ span: 12 }}
            md={{ span: 24 }}
            xs={{ span: 24 }}
            className="d-flex justify-content-center align-items-center"
          >
            <div className=" col-md-24 col-lg-12 col-xl-10 login-box justify-content-center d-flex">
              <Spin spinning={this.state.loading} size="large">
                {" "}
                {this.state.error == "true" && (
                  <Alert
                    message="Invalid Login! Please try again"
                    type="error"
                    showIcon
                  ></Alert>
                )}
                <Row className="mb-4 d-none d-lg-block">
                  <h2>Sign In</h2>
                </Row>
                <Form
                  className="floating-label"
                  {...layout}
                  onSubmit={this.handleLogin}
                  ref={(c) => {
                    this.form = c;
                  }}
                  name="basic"
                  initialValues={{
                    remember: true,
                  }}
                >
                  <FloatLabel label="Email" value={this.state.email}>
                    <Form.Item
                      name="email"
                      rules={[
                        {
                          required: true,
                          message: "Please enter your email!",
                          type: "email",
                        },
                      ]}
                    >
                      <Input
                        type="Email"
                        className="input-control"
                        value={this.state.email}
                        onChange={this.onChangeUsername}
                      />
                    </Form.Item>
                  </FloatLabel>
                  <FloatLabel label="Password" value={this.state.password}>
                    <Form.Item
                      name="password"
                      rules={[
                        {
                          required: true,
                          message: "Please enter your password!",
                        },
                      ]}
                    >
                      <Input.Password
                        type="password"
                        className="input-control"
                        value={this.state.password}
                        onChange={this.onChangePassword}
                      />
                    </Form.Item>
                  </FloatLabel>
                  <div className="forgot-pwd">
                    <a href="#">Forgot Password</a>
                  </div>

                  <Form.Item {...tailLayout}>
                    <Button
                      disabled={!isEnabled}
                      type="primary"
                      htmlType="submit"
                      className="primary-btn-lg w-100 mt-4"
                      onClick={() => {
                        this.handleLogin();
                      }}
                    >
                      SIGN IN
                    </Button>
                  </Form.Item>
                </Form>
              </Spin>
            </div>
          </Col>
        </Row>
      </>
    );
  }
}

function mapStateToProps(state) {
  const { isLoggedIn } = state.auth;
  const { message } = state.message;
  return {
    isLoggedIn,
    message,
  };
}
export default connect(mapStateToProps)(Login);
